/* SCSS Document */

@charset "UTF-8";

@import "../component/_color";
@import "../component/_font";
@import "../component/_grid";
@import "../../foundation/_bbbb";

@mixin boxitem{ 
    color: $white;
    font-size: $font33;
    display: flex;
    align-items: center;
    justify-content: center;
}

.main{
    height: 100%;
    display: flex;
    flex-direction: column;
    &__box{
        &--A{
            flex-grow: 0;
            & .boxitem{
                &--1{
                    @include boxitem;
                    background-color: $orange;
                    height: 100px;
                }
            }
        }
        &--B{
            height: 100%;
            display: flex;
            flex-direction: row;
            flex-grow: 1;
            justify-content: center;
            & .boxitem{
                &--2{
                    @include boxitem;
                    background-color: $green;
                    flex-grow: 0;
                    flex-shrink: 0;
                }
                &--3{
                    @include boxitem;
                    background-color: $blue;
                }
            }
        }
    }
}

/*Phone*/
@media (max-width: $grid-tablet-breakpoint - 1) {
    .main{
        &__box{
            &--B{
                flex-direction: column;
                & .boxitem{
                    &--2{
                        height: 50%;
                    }
                    &--3{
                        min-width: 60px;
                        height: 50%;
                    }
                }
            }
        }
    }
}

/*Tablet*/
@media (min-width: $grid-tablet-breakpoint) and (max-width: $grid-desktop-breakpoint - 1) {
    .main{
        &__box{
            &--B{
                & .boxitem{
                    &--2{
                        order: 2;
                        flex-basis: 300px;
                    }
                    &--3{
                        flex-grow: 1;
                        order: 1;
                    }
                }
            }
        }
    }
}

/*PC*/
@media (min-width: $grid-desktop-breakpoint) {
    .main{
        &__box{
            &--B{
                & .boxitem{
                    &--2{
                        order: 1;
                        flex-basis: 300px;
                    }
                    &--3{
                        flex-grow: 1;
                        order: 2;
                    }
                }
            }
        }
    }
}
